/* STATIC STYLES (desktop-first) */

.experience {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}

.experience-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.experience-cards > * {
  flex: 0 0 20rem;
}

.experience-card {
  text-align: center;
}

.experience-card-header {
  margin-bottom: 1rem;
  background: linear-gradient(90deg, #00B5D8, #6EE7B7);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.experience-card-inner {
  display: inline-block;
  text-align: left;
}

.experience-card-inner p {
  display: flex;
  align-items: center;
  gap: .75rem;
}

.experience-balls {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.experience-balls > div {
  width: 8rem !important;
  height: 8rem !important;
}

/* RESPONSIVE STYLES */

@media (max-width: 1200px) {

}

@media (max-width: 992px) {

}

@media (max-width: 768px) {
  .experience {
    padding-inline: 8rem;
  }

  .experience-cards {
    flex-direction: column;
  }

  .experience-cards > * {
    flex: 1;
  }
}

@media (max-width: 576px) {
  .experience {
    padding-inline: 2rem;
  }

  .experience-balls {
    display: none;
  }
}